.Gameboard {
  color: white;
  max-width: 540px;
  margin: auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  .Board {
    display: flex;
    justify-content: center;
    background-color: #bbada0;
    padding: 5px;
    padding-bottom: 10px;
    border-radius: 1rem;
    flex-direction: column;

    .Row {
      display: flex;

      .Cell {
        // border: 5px solid darkgoldenrod;
        text-align: center;
        border: 10px solid transparent;

        &.Selected {
          border: 10px solid darkgoldenrod;
          transition: border 250ms ease;
        }

        margin: 5px;
        border-radius: 1rem;
        cursor: pointer;

        &[data-animation='FlipOut'] {
          animation-name: FlipOut;
          animation-duration: 0.5s;
          animation-timing-function: ease-in;
          animation-fill-mode: forwards;
          animation-iteration-count: 1;
        }

        &[data-animation='FlipIn'] {
          animation-name: FlipIn;
          animation-duration: 0.5s;
          animation-timing-function: ease-in;
          animation-fill-mode: forwards
        }

        &[data-animation='PopIn'] {
          animation-name: PopIn;
          animation-duration: 0.5s;
          animation-timing-function: ease-in;
          animation-iteration-count: 1;
        }
      }
    }


  }

  .GameOver {
    position: fixed;
    background: goldenrod;
    padding: 2rem;
    border-radius: 1rem;
    border: 3px solid black;
    color: black;
    text-align: center;
    .Title {
      color: red;
      font-size: 2rem;
      font-weight: bold;
    }
    .Desc {
    }
    .Restart {
      margin-top: 1rem;
      text-transform: uppercase;
      border: none;
      padding: 0.5rem;
      background-color: purple;
      color: white;
      font-weight: bold;
    }
  }

  @keyframes PopIn {
    from {
      transform: scale(0.8) rotate(0);
      opacity: 0;
    }

    40% {
      transform: scale(1.1);
      opacity: 1;
    }

    100% {
      transform: scale(1) rotate(0deg);
      opacity: 1;
    }
  }

  @keyframes FlipOut {
    0% {
      transform: rotateX(0);
    }

    100% {
      transform: rotateX(-90deg);
      opacity: 0;
    }
  }

  @keyframes FlipIn {
    0% {
      transform: rotateX(-90deg);
      opacity: 0;
    }

    100% {
      transform: rotateX(0deg);
      opacity: 1;
    }
  }
}